import React, { useState } from 'react';
import './index2.css';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import ListCom from './ListCom';





const TodoList = () => {

    const [ValueY, setValue] = useState();
    const [ValueList, setValueList] = useState([]);

    const Value1 = (event) => {
        setValue(event.target.value);

    };
    const CreateList = () => {
        setValueList((OldVal) => {
            return [...OldVal, ValueY];
        });
        setValue(" ");
    };

    return (


        <>


            <div className='MainDiv'>

                <div className='CenterDiv'>

                    <br />
                    <h1>TO DO List</h1>
                    <br />

                    <input type='Text' value={ValueY} placeholder='Add a Item' onChange={Value1}>


                    </input>

                    <Button onClick={CreateList} className="newBtn"><AddIcon /></Button>
                    <ol>
                        {ValueList.map((val, index) => {
                            return <>
                            <ListCom key={index} text={val} />
                      
                                </>;
                        })}
                    </ol>
                </div>
            </div>

        </>
    );
};


export default TodoList;